<template>
  <div class="contain">
      
      <cropper ref="cropper" :img="option.img" :outputSize="option.size" :outputType="option.outputType" :autoCrop="option.autoCrop "
      :canScale="option.canScale" :fixed="option.fixed" :fixedBox="option.fixedBox" :fixedNumber="option.fixedNumber"
      @realTime="realTime"></cropper>
      <div class="show-preview" :style="{'width': previews.w + 'px', 'height': previews.h + 'px',  'overflow': 'hidden',
      'margin': '5px'}">
      <div :style="previews.div">
        <img :src="option.img" :style="previews.img">
      </div>
      </div>

      <div class="oper">  
          <button type="button"><input  type="file" @change="uploadImg($event)" value="上传图片">上传图片</button>
          <button type="button" @click="cropperImg">完成</button>
      </div>
      <div class="oper">
          <button type="button" @click="startCropper">开始截图</button>
          <button type="button" @click="stopCropper">停止截图</button>
      </div>
      <div class="oper">
          <button type="button" @click="clearCropper">清除截图</button>
          <button type="button" @click="rotateLeft">向左边旋转90度</button>
      </div>
       <div class="oper">
         <button type="button" @click="rotateRight">向右边旋转90度</button>
         <button type="button" @click="changeScaleBig">放大</button>
       </div>
        <div class="oper">
         <button type="button" @click="changeScaleSmall">缩小</button>
         <button type="button" @click="preview('base64')">预览</button>
       </div>
       <div class="oper">
            <button type="button" @click="down('base64')">下载</button>
       </div>
       <!-- <a @click="down('base64')" class="btn" :href="downImg" download="demo">download(base64)</a> -->
       <div v-if="cropperPreview" class="cropper-preview" @click="hidePreview"><img :src="cropperPreview"/></div>
  </div>
</template>
<script>
import VueCropper from "vue-cropper";
import axios from "axios";
export default {
  name: "crpopper",
  data() {
    return {
      data: "crpopper",
      previews: {},
      downImg: "",
      option: {
        img: "https://fengyuanchen.github.io/cropper/images/picture.jpg",
        size: 1,
        outputType: "jpg",
        autoCrop: false,
        canScale: true,
        fixedBox: false,
        fixed: true,
        fixedNumber: [1, 1]
      },
      uptoken: "",
      domain: "",
      cropperPreview:""
    };
  },
  components: {
    cropper: VueCropper
  },
  mounted() {
    this.getQiniuConfig();
  },
  methods: {
    realTime(data) {
      this.previews = data;
    },
    getQiniuConfig() {
      axios
        .get("http://api.fans.smi170.com/api/V1/common/resource/qiniu/config")
        .then(res => {
          this.domain = res.data.domain;
          this.uptoken = res.data.uptoken;
        });
    },
    uploadImg($event) {
      var form = new FormData();
      form.append("file", $event.target.files[0]);
      form.append("token", this.uptoken);
      axios.post("http://up-z2.qiniu.com/", form).then(res => {
        this.option.img = `${this.domain}${res.data.key}`;
      });
    },
    startCropper() {
      this.$refs.cropper.startCrop(); //开始截图
    },
    stopCropper() {
      this.$refs.cropper.stopCrop(); //停止截图
    },
    clearCropper() {
      this.$refs.cropper.clearCrop();
    },
    rotateLeft() {
      this.$refs.cropper.rotateLeft();
    },
    rotateRight() {
      this.$refs.cropper.rotateRight();
    },
    changeScaleBig() {
      this.$refs.cropper.changeScale(1);
    },
    changeScaleSmall() {
      this.$refs.cropper.changeScale(-1);
    },
    preview(type) {
      // var test = window.open("about:blank");
      // test.document.body.innerHTML = "图片生成中..";
      this.$refs.cropper.getCropData(data => {
        this.cropperPreview = data;
        this.downImg = data;
        // console.log(data);
        // test.document.body.innerHTML = '<img src='+data+'/>';
      });

      // if (type === "blob") {
      //   this.$refs.cropper.getCropBlob(data => {
      //     var test = window.open("");
      //     test.location.href = window.URL.createObjectURL(data);
      //   });
      // } else {
      //   this.$refs.cropper.getCropData(data => {
      //     // test.location.href = data
      //     console.log(data);
      //   });
      // }
    },
    down(type) {
      event.preventDefault()
      var aLink = document.createElement("a");
      aLink.download = "demo";
      // 输出
      if (type === "blob") {
        this.$refs.cropper.getCropBlob(data => {
          aLink.href = data;
          aLink.click();
        });
      } else {
        this.$refs.cropper.getCropData(data => {
          aLink.href = data;
          aLink.click();
        });
      }
    },
    cropperImg() {
      //   console.log(this.$refs.cropper.cropW + "w"); // 截图框宽度
      //   console.log("h" + this.$refs.cropper.cropH);  //截图框高度
      this.$refs.cropper.changeScale(1);
    },
    hidePreview(){
      this.cropperPreview = '';
    }
  }
};
</script>
<style scoped="scoped">
.contain {
  height: 90%;
}
button {
  background: #70c75b;
  color: #fff;
  width: 48%;
  float: left;
  height: 40px;
  line-height: 40px;
  font-size: 16px;
  border-radius: 4px;
  margin-right: 4%;
  position: relative;
}
input {
  opacity: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
}
.oper {
  padding: 5px 10px;
}
.oper:after {
  content: "";
  display: block;
  clear: both;
}
button:last-child {
  margin-right: 0%;
}
.cropper-modal {
  display: none !important;
}
.cropper-preview {
  position: fixed;
  left: 0;
  top: 0;
  background: rgba(0, 0, 0, 0.8);
  height: 100%;
  width: 100%;
  padding: 200px;
  box-sizing: border-box;
}
.cropper-preview img {
  width: 100%;
}
</style>





