<template>
    <div>
        <table border="0" cellpadding="0" cellspacing="0">
            <tr>
                <td :class="{active:index==1}" style="background-image:url(http://www.lanrenzhijia.com/demos/38/3848/demo/img/1.png)"></td>
                <td :class="{active:index==2}" style="background-image:url(http://www.lanrenzhijia.com/demos/38/3848/demo/img/2.png)"></td>
                <td :class="{active:index==3}" style="background-image:url(http://www.lanrenzhijia.com/demos/38/3848/demo/img/3.png)"></td>
            </tr>
            <tr>
                <td :class="{active:index==8}" style="background-image:url(http://www.lanrenzhijia.com/demos/38/3848/demo/img/4.png)"></td>
                <td class="btn" @click="draw"></td>
                <td :class="{active:index==4}" style="background-image:url(http://www.lanrenzhijia.com/demos/38/3848/demo/img/5.png)"></td>
            </tr>
            <tr>
                <td :class="{active:index==7}" style="background-image:url(http://www.lanrenzhijia.com/demos/38/3848/demo/img/6.png)"></td>
                <td :class="{active:index==6}" style="background-image:url(http://www.lanrenzhijia.com/demos/38/3848/demo/img/7.png)"></td>
                <td :class="{active:index==5}" style="background-image:url(http://www.lanrenzhijia.com/demos/38/3848/demo/img/8.png)"></td>
            </tr>
        </table>

        <!-- <MarqueeTips content="" style="color: red" :speed="10">
                    <span>你搜发你搜房你搜非农为非农违反 我是一个比较长的提示！拉吧拉吧拉吧拉吧拉吧拉吧拉吧拉吧拉吧拉吧拉吧拉吧~~~~ </span>
                    <span></span>
                </MarqueeTips> -->
        <marquee direction="up" hspace="50" vspace="10" scrollamount="1" scrolldelay="100" height='20'>
            <div>设定活动字幕的滚动方</div>
            <div>呦呵</div>
        </marquee>

    </div>
</template>
<script>
// import { mapGetters } from 'vuex';
import { Toast } from "mint-ui";
import MarqueeTips from "vue-marquee-tips";
export default {
  data() {
    return {
      index: -1,
      prize: 6,
      speed: 50,
      cycle: 80,
      times: 0,
      click: true,
      turn: true
    };
  },
  components: {
    MarqueeTips: MarqueeTips
  },
  mounted() {
    this.name = this.$route.params["name"];
    Toast({
      message: `Hello! ${this.name}`,
      iconClass: "iconfont icon-xiaolian2",
      position: "middle",
      duration: 3000
    });
  },
  methods: {
    draw() {
      if (!this.turn) {
        return;
      } else {
        // this.$store.dispatch('SET_LOTTERY_STATUS', false);
        // this.$_api.post({
        //     ext: 'http://haokur.com/cross/test',
        //     data: '8',
        //     code: 2
        // })
        //     .then(res => {
        //         this.prize = res.data;
        //     })
        //     .catch(err => {
        //         console.log('请求错误')
        //         throw '错误'
        //     })
        //     .finally(() => {
        //         // console.log('请求完成')
        //         // this.$_showtip('请求完成')
        //     })
        this.roll();
      }
    },
    roll() {
      setTimeout(() => {
        this.times++;
        if (this.times > this.cycle + 8 && this.prize == this.index) {
          // this.$store.dispatch('SET_LOTTERY_STATUS', true);
          alert("你中奖啦");
          this.times = 0;
          this.speed = 50;
          return;
        } else if (this.times == this.cycle) {
          this.prize = Math.ceil(Math.random() * 8);
          this.speed += 100;
        } else if (this.times > 120) {
          this.index = -1;
          this.times = 0;
          this.speed = 50;
          alert("网络异常请稍候再试");
          // this.$store.dispatch('SET_LOTTERY_STATUS', true)
          return;
        }
        this.luck();
        this.roll();
      }, this.speed);
    },
    luck() {
      this.index++;
      if (this.index > 8) {
        this.index = 1;
      }
    },
    test() {
      var arr = [],active=0;
      for (var i = 0; i < 10; i++) {
        if (i != 5) {
             arr.push({ value: 'null' });
        } else {
          arr.push({ value: active });
          active++;

        }
      }
    }
  }
};
</script>
<style src="./lottery.scss" lang="scss" scoped="scoped">

</style>





