import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import Login from '@/pages/login/login'
import Home from '@/pages/home/home'
import Lottery from '@/pages/lottery/lottery'
// import Crpopper from '@/pages/crpopper/crpopper'
import Axios from '@/pages/axios/axios'
import Screenshot from '@/pages/screenshot/screenshot'
import Crpopper from '@/pages/crpopper/crpopper'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    {
      path: '/home/:name',
      name: 'Home',
      component: Home
    },
    {
      path: '/hello',
      name: 'Hello',
      component: Hello
    },
    {
      path: '/lottery/:name',
      name: 'Lottery',
      component: Lottery
    },
    {
      path: '/crpopper',
      name: 'Crpopper',
      component: Crpopper
    },
    {
      path: '/axios',
      name: 'Axios',
      component: Axios
    },
    {
      path: '/sreenshot',
      name: 'Screenshot',
      component: Screenshot
    },
  ]
})



