<template>
    <section>
        <input type="file" value="上传" @change="uploadImg($event)">
    </section>
</template>

<script>
import { Toast } from "mint-ui";
export default {
  data() {
    return {
      name: "screenshot"
    };
  },
  mounted() {},
  methods: {
    uploadImg($event) {
      let file = $event.target.files[0];
      let fileType = file.type;
      let fileReader = new FileReader();
      fileReader.readAsDataURL(file);
      fileReader.onload = function(event) {
        let img = new Image();
        img.src = event.target.result;
        img.onload = function() {
          console.log(img.width);
          console.log(img.height);
          let scale = img.width / img.height;
          let img_w = 100;
          let img_h = img_w / scale;
          var cvs = document.createElement("canvas");
          cvs.width = img_w;
          cvs.height = img_h;
          let cvx = cvs.getContext("2d");
          cvx.drawImage(img, 0, 0, img_w, img_h);
          let newImg = cvs.toDataURL(fileType,0.7)
          console.log(newImg)
        };
      };
    }
  }
};
</script>
<style>

</style>



