<template>
    <section>
        <div class="bg"></div>
        <div class="container">
            <div class="title">
                {{title}}
            </div>
            <div class="n-input">
                <i class="iconfont icon-icon_member"></i>
                <input type="text" v-model="name">
            </div>
            <div class="n-input">
                <!-- <button type="button" v-link="{name:'Home'}">进入首页</button> -->
                <button type="button" @click="login">进入首页</button>
            </div>
        </div>
    </section>
</template>
<script>
import { Toast } from 'mint-ui';
export default {
    data() {
        return {
            title: 'welcome on!',
            name: ''
        }
    },
    mounted() {

    },
    methods: {
        login() {
            if (this.name) {
                this.$router.push({
                    path: `/home/${this.name}`
                })
            } else {
                Toast({
                    message: '请输入您的姓名',
                    position: 'bottom',
                    duration: 3000
                });
            }
        }
    }
}
</script>
<style src="./login.scss" lang="scss" scoped></style>




