<template>
  <div>
      {{data}}
  </div>
</template>
<script>
import axios from "axios";
export default {
  name: "axios",
  data() {
    return {
      data: "test"
    };
  },
  mounted() {
    this.instance = axios.create({
      baseURL: "http://testsmi.assets.smi170.com/communication/",
      timeout: 1000,
      headers: { "Content-Type": "application/x-www-form-urlencoded" }
    });
    // axios.defaults.baseURL = "http://testsmi.assets.smi170.com/communication/";
    // // axios.defaults.headers.common["Authorization"] = AUTH_TOKEN;
    // axios.defaults.headers.post["Content-Type"] =
    //   "application/x-www-form-urlencoded";
    this.configRequest();
    // this.defaultRequest();
    // this.concurrentRequest();
    // this.baseRequest();
  },
  methods: {
    configRequest() {
      this.instance
        .get(
          "tel/getTicket?token=NzMzZGUyZTctZWRlMC00YWNjLWFkZGQtYWI4ZjhlMTQxMDUx"
        )
        .then(res => {
          console.log(res);
            console.log(res.data.hits.hits[0])
        })
        .catch(error => {
          console.log(error.message);
        if(error.message.indexOf('timeout')>-1){
            alert('超时了！')
        }
        });
    },
    defaultRequest() {
      // Send a GET request (default method)
      axios(
        "http://testsmi.assets.smi170.com/communication/tel/getHomepage?token=NzMzZGUyZTctZWRlMC00YWNjLWFkZGQtYWI4ZjhlMTQxMDUx",{timeout: 5000}
      ).then(() => {
        console.log(res);
      });
    },
    concurrentRequest() {
      // Performing multiple concurrent requests
      // this.getUserAccount();
      // this.getUserPermissions();
      axios.all([this.getUserAccount(), this.getUserPermissions()]).then(
        axios.spread(function(acct, perms) {
          // Both requests are now complete
          console.log(acct);
          console.log(perms);
        })
      );
    },
    getUserAccount() {
      return axios.get(
        "http://testsmi.assets.smi170.com/communication/tel/getHomepage?token=NzMzZGUyZTctZWRlMC00YWNjLWFkZGQtYWI4ZjhlMTQxMDUx"
      );
    },
    getUserPermissions() {
      return axios.get(
        "http://testsmi.assets.smi170.com/communication/tel/getUserInfo?token=NzMzZGUyZTctZWRlMC00YWNjLWFkZGQtYWI4ZjhlMTQxMDUx"
      );
    },
    baseRequest() {
      //   performing a get request
      axios
        .get(
          "http://testsmi.assets.smi170.com/communication/tel/getHomepage?token=NzMzZGUyZTctZWRlMC00YWNjLWFkZGQtYWI4ZjhlMTQxMDUx"
        )
        .then(function(response) {
          console.log(response);
        })
        .catch(function(error) {
          console.log(error);
        });
      //   performing a post request
      axios
        .post(
          "http://testmall.xingmeihui.com/smiweb/api/user/recharge/V2/getRechList",
          {
            token: "NzMzZGUyZTctZWRlMC00YWNjLWFkZGQtYWI4ZjhlMTQxMDUx"
          }
        )
        .then(function(response) {
          console.log(response);
        })
        .catch(function(error) {
          console.log(error);
        });
    }
  }
};
</script>



